body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.annoy-logo img {
  border-radius: 70%;
  WIDTH: 50%;
}

.annoy-container {
    display: grid;
    grid-template-columns: 2fr 5fr 3fr;
    grid-template-rows: auto 5fr 1fr;
    grid-gap: 10px;
    height: 99vh;
    width: 100%;
    background:#f8f9fa;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.12), inset 0 -1px 0 0 #dadce0;
    grid-row-gap:0;
}
.msg-box {
    grid-column: span 3;
}
header {
    grid-column: span 3;
}

div {
    border-radius : 5px;    
}
ul {
    list-style: none;
    text-align: left;
    margin: 0;
    padding : 10px;
}
.messages, .users {
    word-break: break-all;
    overflow-y: auto;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.12), inset 0 -1px 0 0 #dadce0;
}
header {
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.12), inset 0 -1px 0 0 #dadce0;
    padding: 5px; 
    min-height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
}

.annoy-section {
    word-break: break-all;
    overflow-y: auto;
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.12), inset 0 -1px 0 0 #dadce0;
}

img {
    width: 200px;
}
/* Common styles */
.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  a {
    text-decoration: none;
    font-weight: bold;
    color:#1a73e8;
  }
  button {
    background: #4285f4;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
  }
  .grid-container-2 {
    display: flex;
    justify-content: flex-end;
    border:none
  }
  fieldset {
    text-align: left;
    margin: 0;
    border-radius: 5px;
    margin-bottom: 10px;
    border-color: #4285f4;
  }
  fieldset legend {
    font-size: 0.7rem;
    color: #1a73e8;
    font-weight: bold;
  }
  input[type="text"]:focus, input[type="password"]:focus {
    outline: #fff;
  }
  fieldset input {
    width: 98%;
    height: 30px;
    border: none;
    border-radius: 5px;
    padding:5px;
  }
  /* fieldset.focusme {
    border-color: #4285f4;
    border-width: 2px;
  } */
  .keep-right {
    float: right;
  }
